@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600;700&display=swap);
body {
	font-family: 'Source Sans Pro', sans-serif;
  margin: 0;
}

.App {
  padding: 0 10%;
}

#headerlogo-container {
    width: 200px;
    margin: 15px auto 0;
}

#headerlogo {
    width: 100%;
}

#shortShow-container {
    background-color: rgb(250, 250, 250);
    border-radius: 15px;
    box-shadow: 5px 5px 10px rgb(218, 218, 218);
    padding: 10px;
}

#shortShowImages-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

#shortShowImages-container img {
    width: 100%;
    height: 400px;
    object-fit: fill;
    border-radius: 15px;
}

#shortShowInfo-container {
    text-align: center;
}

#shortShowInfo-container h1 {
    color: rgb(27, 99, 99);
    font-weight: 300;
    line-height: 15px;
}


#shortShowInfo-container p {
    font-weight: 200;
    font-size: large;
    line-height: 10px;
}

#shortShowInfo-container span {
    font-weight: 800;
}

#shortShowInfo-detailsbtn {
    text-decoration: none;
    display: inline-block;
    font-size: large;
    padding: 10px 100px;
    border-radius: 15px;
    border-color: transparent;
    color: white;
    background-color: rgba(205, 92, 92, 0.781);
}
#shows-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
    padding: 5% 10%;
}

#detailedShow-container {
    display: flex;
    justify-content: space-around;
    padding: 5% 10%;
}

#detailedShowImage-container {
    width: 45%;
}

#detailedShowImage-container img {
    width: 100%;
    height: 100%;
    border-radius: 10px;
}

#detailedShowInfo-container {
    width: 45%;
    text-align: center;
    position: relative;
}

#detailedShowInfo-container h1 {
    font-size: 50px;
}

#bookingForm {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#bookingForm input {
    display: block;
    text-align: center;
    margin: 10px;
    padding: 5px 0;
    border-radius: 10px;
    font-size: large;
}

#detailedShowInfo-homebtn {
    font-size: large;
    text-decoration: none;
    position: absolute;
    top: 0;
    right: 0;
    background-color: rgb(1, 201, 201);
    color: white;
    padding: 5px 10px;
    border-radius: 10px;
}

.detailedShowInfo-bookbtn{
    font-size: large;
    width: 100%;
    text-decoration: none;
    border-color: transparent;
    background-color: rgb(1, 201, 201);
    color: white;
    padding: 5px 10px;
    border-radius: 10px;
}

#loadingData {
    text-align: center;
    font-weight: 400;
    margin-top: 100px;
}

#bookingManagement{
    background-color: azure;
    padding: 10px;
    border-radius: 10px;
}

#bookingManagement span {
    font-weight: 800;
}

